import { faArrowAltCircleUp, faArrowAltCircleDown } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState } from 'react';
import { SketchPicker } from 'react-color';
import styled, { keyframes } from 'styled-components';
import Fish from './widgets/fish';


function App() {

  const saveSvgAsPng = require('save-svg-as-png')
  const saveFile = () => {
    saveSvgAsPng.saveSvgAsPng(document.getElementById('fishsvg'), 'myFish.png')
  }
  const [color, SetColor] = useState({
    r: 19,
    g: 116,
    b: 186,
    a: 1,
    hex: "#1374BA"
  });
  const [hex, setHex] = useState('#1374BA');
  const [r, setR] = useState("19");
  const [g, setG] = useState("116");
  const [b, setB] = useState("186");
  const [a, setA] = useState("1");
  const [up, setUp] = useState(true);

  const handleChange = ({ rgb, hex }) => {
    SetColor(rgb);
    setR(rgb.r);
    setG(rgb.g);
    setB(rgb.b);
    setA(rgb.a);
    setHex(hex);
  }
  const presets = [
    "#c7adf0",
    '#ba445c',
    '#a3636c',
    '#c28890',
    '#781924',
    '#d67a8c',
    '#63151e',
    '#47020b',
    '#ffebed',
    '#45000a',
    "#ffffff"
  ];

  const [fillColors, setFillColors] = useState(Array(70).fill('#ffffff'))

  const onFillColor = (i) => {
    let newFillColors = fillColors.slice(0)
    newFillColors[i] = hex
    setFillColors(newFillColors)
  }

  const Pallette = () => {

    const ArrowIcon = styled(FontAwesomeIcon)`
    color: purple;
    font-size: 30px;
    padding-right: 10px;
  `;
    const Row = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: rgba(255,255,255,0.9);
    padding: 0 20px;
  `;
    const WrapP = styled.div`
    display: flex;
    flex-direction: row;
    z-index: 1;
  `;
    const Label = styled.div`
  
  `;
    const Square = styled.div`
  padding: 20px;
  background-color: ${({ color }) => color};
  margin: 10px 0;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
`;
    const Div2 = styled.div`
  position: fixed;
  bottom: ${({ up }) => up ? '20px' : '-50px'};
  left: 50%;
  transform: translatex(-50%);
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: bottom 300ms linear;
`;
    return (
      <Div2 up={up}>
        <Row>
          <ArrowIcon onClick={() => setUp(!up)} icon={up ? faArrowAltCircleDown : faArrowAltCircleUp} />
          <Label>Default Color Palette</Label>
        </Row>
        <WrapP>
          {presets.map((i) =>
            <Square color={i} onClick={(e) => { setHex(i); SetColor(i) }} />
          )}</WrapP>
      </Div2>
    )
  };

  return (
    <Wrap>
      <Center color={hex}>
        Color the fish by choosing a color, then clicking on where you want to color.
      </Center>
      <Reset>
        <Button onClick={saveFile}>Download Your Fish</Button>
        <Button onClick={() => setFillColors((Array(70).fill('#ffffff')))}>reset</Button>
      </Reset>
      <Inner>
        {/* <CImg fillColors={fillColors} onFill={onFillColor} fillme={hex} /> */}
        <Fish fillColors={fillColors} onFill={onFillColor} />
        <Div>
          <SketchPicker color={color} onChange={handleChange} presetColors={presets} />
        </Div>
      </Inner>
      <Pallette />
    </Wrap>
  );
}

export default App;

const Button = styled.button`
  background: #ffffff;
  color: #5B0072;
  outline: none;
  border: 2px solid #5B0072;
  font-size: 16px;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  transition: all 300ms linear;
  margin: 0 10px;
  &:hover {
     background: #5B0072;
     color: white;
  }
`;

const Reset = styled.div`
  display: flex;
  justify-content: center;
  margin: 20px;
`;
const Center = styled.div`
  display: flex;
  justify-content: center;
  margin: 20px;
  font-size: 20px;
  font-weight: 600;
  color: ${({ color }) => color};
`;

const Wrap = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: #F9FBFD;
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: fa5-proxima-nova,"Helvetica Neue",Helvetica,Arial,sans-serif;
`;

const Inner = styled.div`
  margin: 0 auto;
`;

const Div = styled.div`
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
`;